import React from 'react'
import {Link} from "react-router-dom"

const links = [
    {
        route:"/",
        title: "HOME"
    },
    {
        route:"/dashboard",
        title: "DASHBOARD"
    },
    {
        route:"/login",
        title: "LOGIN"
    }
]

const Navbar = () => {
    return (
        <div style={{display:"flex", justifyContent:"center"}}>
            {
                links?.map(item => (
                    <Link style={{margin:"10px"}} to={item.route}>{item.title}</Link>
                ))
            }
        </div>
    )
}

export {Navbar}
